import SelectAutoWidth from "./selectBox";

export default function FilterTodo({
  searchInput,
  setSearchInput,
  handleSearch,
  selectStatus,
  searchParams,
  router,
}) {
  return (
    <>
      <div className="flex items-center gap-2">
        <input
          className="text-black rounded-lg border border-gray-500 focus:ring-2 focus:ring-indigo-400 px-4 py-2 w-95 outline-none transition"
          type="text"
          value={searchInput}
          onChange={(e) => setSearchInput(e.target.value)}
          onKeyDown={(e) => {
            if (e.key === "Enter") handleSearch(searchInput);
          }}
          placeholder="Enter keyword"
        />
      </div>
      <div className="flex items-center gap-2">
        <SelectAutoWidth selectStatus={selectStatus} searchParams={searchParams} router={router}/>
      </div>
    </>
  );
}
